@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  position: fixed;
  z-index: 1000;
  margin-top: 0;
  background-color: #e8d1e4;
  width: 100%;
  height: 70px;
}

.navlink, .toggler {
  display: flex;
  text-align: center;
  flex-direction: row;
  vertical-align: middle;
  margin: 20px;
  padding-top: 20px;
  color: #fff;
  text-decoration: none;
  font-family: "BioRhyme", serif;
  text-shadow: rgba(7, 93, 133) 2px 2px;
}

.toggler {
  padding: 0;
}

.navlink:hover,
.social-links:hover,
.toggler:hover,
.nav-title a:hover {
  color: #f9f871;
  cursor: pointer;
  transition: transform 0.3s cubic-bezier(0, 0.52, 0, 1);
}

.social-links {
  visibility: visible;
  display: flex;
  margin: 20px;
  justify-content: space-between;
  color: #323232;
  text-decoration: none;
  font-family: "BioRhyme", serif;
}
.navlink:active {
  color: #f9f871;
  text-decoration: underline;
}
.nav-title a {
  z-index: 1;
  display: flex;
  text-align: center;
  font-size: 1.35em;
  margin-left: 30px;
  margin-right: 10px;
  color: #fff;
  text-decoration: none;
  font-family: "BioRhyme", serif;
  text-shadow: rgba(7, 93, 133) 2px 2px;
  min-width: 300px;
}

  @media (max-width: 768px) {
  .nav-title {
    visibility: hidden;
  }
  .navlink {
    position: fixed;
    display: flex;
  }
  .social-links {
    flex-direction: column;
  }
}
.dropdown-menu {
  background-image: linear-gradient(
    to bottom,
    #e8d1e4,
    #f0cfdf,
    #f7cdd8,
    #fcccd0,
    #ffccc6,
    #ffcdbc,
    #ffcfb0,
    #ffd2a6,
    #ffd998,
    #ffe28a,
    #ffec7c,
    #f9f871
  );
  transition: 0.6s ease-in-out;
}

