.carousel {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: auto;
  height: auto;
  margin: 0 auto;
  background-color: inherit;
  padding: 40px;
  justify-content: space-around;
}

.social {
  position: absolute;
  background-color: inherit;
  margin-top: 10px;
  bottom: 10px;
}

.social a {
  justify-content: space-around;
  font-size: 20px;
  bottom: 10px;
  color: #323232;
}

.social a:hover {
  color: #f9f871;
}

@media all and (min-width: 200px) {
  .social {
