:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  box-sizing: border-box;
}

*:focus {
  outline: 0;
}

.App {
  position: relative;
  display: flex;
  width: 100vw;
  min-height: 100vh;
  scroll-behavior: scroll;
}

html {
  font-family: "BioRhyme", serif;
  line-height: 1.15;
  -webkit-tap-highlight-color: transparent;
  display: block;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  overflow: scroll;
  height: 100%;
  width: 95%;
  min-height: 100%;
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #323232;
  text-align: left;
  background-color: #e8d1e4;
}

@media (max-width: 610px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }

  .App {
    flex-direction: column;
    margin: 0;
    padding: auto;
  }
}

